const $disableScroll = document.querySelectorAll('.js-menu-open');
const $enableScroll = document.querySelectorAll('.js-menu-close');
const $containerScroll = document.querySelector('.top-menu');
const disableScroll = function() {
	scrollLock.disablePageScroll( $containerScroll );
};
const enableScroll = function() {
	scrollLock.enablePageScroll( $containerScroll );
};
$disableScroll.forEach(item => {
	item.addEventListener( 'click', disableScroll );
});
$enableScroll.forEach(item => {
	item.addEventListener( 'click', enableScroll );
});

window.lazySizesConfig = {
	loadHidden: false,
	hFac: 0.5,
	expFactor: 2,
	ricTimeout: 150
};

$( document ).ready( function() {
	function isValidEmail( email ) {
		return /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test( email );
	}
	$( document ).on( 'blur', '.subscribe [type="email"]', function() {
		const $this = $( this ),
			$parent = $this.parent(),
			email = $this.val();
		if ( email === '' ){
			$parent.removeClass( 'has-error has-success' );
		} else if( isValidEmail( email ) ) {
			$parent.removeClass( 'has-error' );
			// $parent.find( '.help-block' ).slideUp( 300 );
		} else {
			$parent.addClass( 'has-error' );
			// $parent.find( '.help-block' ).slideDown( 300 );
			$this.closest( 'form' ).submit( function ( e ) {
				e.stopPropagation();
			});
			// setTimeout(function () {
			// 	$parent.find( '.help-block' ).slideUp( 300 );
			// }, 2000);
		}
	});
	$( document ).on( 'click', '.subscribe-inner', function() {
		$( '.subscribe-form' ).addClass( 'active' );
		$( '.subscribe-form input' ).focus();
	});
	// $( document ).on( 'click', function( e ) {
	// 	if ( $( e.target ).closest( '.subscribe-inner' ).length || $( '.subscribe [type="email"]' ).val().length > 0 )
	// 		return;
	// 	$( '.subscribe-form' ).removeClass( 'active' );
	// 	e.stopPropagation();
	// });

	$( '.wrapper' ).addClass( 'show-text' );

	/* - - - - - - - - - - - - - -       fixed header       - - - - - - - - - - - - - - - */
	function fixedHeader() {
		const top = $( window ).scrollTop();
		let posTop = 1;
		if ( top > posTop ) {
			$( '.header' ).addClass( 'fixed' );
		} else {
			$( '.header' ).removeClass( 'fixed' );
		}
	}
	$( window ).on( 'scroll', function() {
		fixedHeader();
	});
	fixedHeader();

	/* - - - - - - - - - - - - - -       menu toggle       - - - - - - - - - - - - - - - */
	$( document ).on( 'click', '.js-menu-open', function() {
		$( '.header' ).addClass( 'menu-open' );
	});
	$( document ).on( 'click', '.js-menu-close', function() {
		$( '.header' ).removeClass( 'menu-open' );
	});

	function waypointsText() {
		let posTop = $( window ).innerHeight();
		$( '.box-translate, .head-img, .head-button, .head-video, .video-play, .category-box, .catalog-box, .subscribe-form' ).waypoint( function( direction ) {
			posTop += $( this.element ).innerHeight();
			if ( direction === 'down' ){
				$( this.element ).addClass( 'animate' );
			} else {
				$( this.element ).removeClass( 'animate' );
			}
		}, {
			offset: function() {
				return $( window ).innerHeight() + this.element.clientHeight - 60;
			}
		});
	}
	waypointsText();

	function waypointsFooter() {
		$( '.footer, .footer' ).waypoint( function( direction ) {
			if ( direction === 'down' ){
				$( this.element ).addClass( 'animate' );
			} else {
				$( this.element ).removeClass( 'animate' );
			}
		}, {
			offset: '95%'
		});
	}
	waypointsFooter();

	$( '[data-fancybox-video]' ).fancybox({
		video: {
			tpl:
				'<video class="fancybox-video" autoplay playsinline muted loop="loop" controls controlsList="nodownload" poster="{{poster}}">' +
				'<source src="{{src}}" type="{{format}}" />' +
				'Sorry, your browser doesn\'t support embedded videos, <a href="{{src}}">download</a> and watch with your favorite video player!' +
				"</video>",
			format: "", // custom video format
			autoStart: true,
		},
		scrolling: 'hidden',
		helpers: {
			overlay: {
				locked: true
			}
		},
		beforeShow: function(){
			$( 'body' ).css({ 'overflow-y':'hidden' });
		},
		afterClose: function(){
			$( 'body' ).css({ 'overflow-y':'visible' });
		},
	});
});
